.glitch {
  padding-top: 20%;
  padding-left: 1em;
  font-size: 7rem;
  font-weight: 400;
  text-transform: uppercase;
  position: relative;
  z-index: 20;

  text-shadow: 0.05em 0 0 rgba(255, 0, 0, 0.75),
    -0.025em -0.05em 0 rgb(128,128,128)
    ,
    0.025em 0.05em 0 rgb(220,220,220)
    ;

  animation: glitch 500ms infinite;
}

.glitch span {
  padding-top: 20%;
  padding-left: 1em;
  position: absolute;
  top: 0;
  left: 0;
}

.glitch span:first-child {
  padding-left: 1em;
  padding-top: 20%;
  animation: glitch 650ms infinite;
  clip-path: polygon(0 0, 100% 0, 100% 45%, 0 45%);
  transform: translate(-0.025em, -0.0125em);
  /* color: green; */
  opacity: 0.8;
}

.glitch span:last-child {
  padding-left: 1em;
  padding-top: 20%;
  animation: glitch 375ms infinite;
  clip-path: polygon(0 80%, 100% 20%, 100% 100%, 0 100%);
  transform: translate(0.0125em, 0.025em);
  /* color: red; */
  opacity: 0.8;
}

/* https://web.dev/prefers-reduced-motion/#(bonus)-forcing-reduced-motion-on-all-websites */

@keyframes glitch {
  0% {
    text-shadow: 0.05em 0 0 rgb(0,0,0),
      -0.05em -0.025em 0 rgb(128,128,128)
      ,
      -0.025em 0.05em 0 rgb(220,220,220)
      ;
  }
  14% {
    text-shadow: 0.05em 0 0 rgb(0,0,0),
      -0.05em -0.025em 0 rgb(128,128,128)
      ,
      -0.025em 0.05em 0 rgb(220,220,220)
      ;
  }
  15% {
    text-shadow: -0.05em -0.025em 0 rgb(0,0,0),
      0.025em 0.025em 0 rgb(128,128,128)
      ,
      -0.05em -0.05em 0 rgb(220,220,220)
      ;
  }
  49% {
    text-shadow: -0.05em -0.025em 0 rgb(0,0,0),
      0.025em 0.025em 0 rgb(128,128,128)
      ,
      -0.05em -0.05em 0 rgb(220,220,220)
      ;
  }
  50% {
    text-shadow: 0.025em 0.05em 0 rgb(0,0,0),
      0.05em 0 0rgb(128,128,128)
      , 0 -0.05em 0 rgb(220,220,220)
      ;
  }
  99% {
    text-shadow: 0.025em 0.05em 0 rgb(0,0,0),
      0.05em 0 0 rgb(128,128,128)
      , 0 -0.05em 0 rgb(220,220,220)
      ;
  }
  100% {
    text-shadow: -0.025em 0 0 rgb(0,0,0),
      -0.025em -0.025em 0 rgb(128,128,128)
      ,
      -0.025em -0.05em 0 rgb(220,220,220)
      ;
  }
}

@media (prefers-reduced-motion: reduce) {
  *,
  ::before,
  ::after {
    animation-delay: -1ms !important;
    animation-duration: 1ms !important;
    animation-iteration-count: 1 !important;
    background-attachment: initial !important;
    scroll-behavior: auto !important;
    transition-duration: 0s !important;
    transition-delay: 0s !important;
  }
}

